import React from "react";
import Layout from "../components/layout";
import SEO from "../components/seo";
import { graphql, navigate } from "gatsby";
import MapChart from "../components/map";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import { useState, useEffect } from "react";
import GoogleContext from "../components/GoogleContext";

import styled from "@emotion/styled";
import { css } from "@emotion/react";

const RecipeCard = styled.button`
  display: block;
  margin: 0px 10px 0px 0px;
  width: 200px;
  height: 250px;
  padding: 0px 5px 5px 5px;
  border-radius: 26px;
  align-items: start;
  background-color: hsl(233, 8%, 19%);
  justify-content: center;
  border-width: 0;
  text-decoration: none important!;
  box-shadow: inset 0 0 0.5px 1px hsla(0, 0%, 100%, 0.075),
    0 0 0 1px hsla(0, 0%, 0%, 0.05), 0 0.3px 0.4px hsla(0, 0%, 0%, 0.02),
    0 0.9px 1.5px hsla(0, 0%, 0%, 0.045), 0 3.5px 6px hsla(0, 0%, 0%, 0.09);
`;

const RecipeTitle = styled.div`
  font-style: bold;
  font-size: 20px;
`;

const RecipeDescription = styled.div`
  font-size: 14px;
`;

export default function FoodPage({
  data: {
    allMdx: { edges },
  },
}) {
  const [google, setGoogle] = useState(null);

  useEffect(() => {
    if (!google) {
      if (window.google) {
        setGoogle(window.google);
      }
    }

    return () => {
      let script = document.getElementById("googleChartsScript");
      if (script) {
        script.remove();
      }
    };
  }, [google]);

  const plays = [
    ["Country", "Most memorable dish"],
    ["Romania", "Sarmale"],
    ["United Kingdom", "?"],
    ["United States", "Grits"],
    ["France", "Canele"],
    ["Switzerland", "Fondue"],
    ["Ireland", "?"],
    ["Germany", "Wurst"],
    ["Norway", "?"],
    ["Greece", "Dolmades"],
    ["Argentina", "Steak"],
    ["Brazil", "Steak"],
    ["South Korea", "Gogi Guk Su"],
    ["Japan", "Ramen"],
    ["Thailand", "N/A"],
    ["China", "Xiao Long Bao"],
    ["India", "Kheer"],
    ["Laos", "Pla Pao Gluer"],
    ["Georgia", "?"],
    ["Sweden", "Meatballs"],
    ["Poland", "Pierogi"],
    ["Bulgaria", "Cucumber soup"],
    ["Hungary", "Goulash"],
    ["Ethiopia", "?"],
    ["Morocco", "Tagine"],
    ["Canada", "Poutine"],
    ["Mexico", "Burrito"],
    ["Belgium", "Chocolate"],
    ["Spain", "Paella"],
    ["Austria", "Strudel"],
    ["Turkey", "Baklava"],
    ["Nepal", "Momo"],
    ["Vietnam", "Pho"],
    ["Australia", "Fairy Bread"],
    ["Nigeria", "Smoked jollof rice"],
    ["Cuba", "Cubano"],
    ["Russia", "Borscht"],
    ["Estonia", "?"],
    ["Egypt", "?"],
    ["Jamaica", "?"],
    ["South Africa", "Biltong"],
    ["Italy", "Tiramisu"],
    ["Peru", "Ceviche"],
  ];

  const posts = edges
    .map((edge) => edge?.node?.frontmatter)
    .filter((edge) => edge != null);

  posts?.map((post) => {
    console.log(JSON.stringify(post));
  });

  return (
    <Layout currentPage="Interests" currentSubPage="Food">
      <SEO
        title="Food around the world"
        description={
          "A page dedicated to my love for food. I will move my recipes into here, as well as keep track of foods I've eaten from around the world."
        }
        keywords={[
          "Food",
          "World",
          "Map",
          "World Map",
          "Challenge",
          "Around The World",
          "Recipe",
        ]}
      />
      <GoogleContext.Provider value={{ google, setGoogle }}>
        <h2>Recipes</h2>
        <div className="d-flex flex-wrap flex-row">
          {posts?.map((post, index) => (
            <RecipeCard
              key={"recipe" + index}
              onClick={() => navigate(post.slug)}
              aria-label={"Navigate to the recipe for: " + post.title}
            >
              {post.featuredImage?.childImageSharp?.gatsbyImageData && (
                <GatsbyImage
                  css={css`
                    object: cover;
                    width: 180px;
                    height: 140px;
                    border-radius: 20px;
                  `}
                  image={getImage(
                    post.featuredImage?.childImageSharp?.gatsbyImageData
                  )}
                />
              )}
              <RecipeTitle>{post.title}</RecipeTitle>
              <RecipeDescription>{post.description}</RecipeDescription>
            </RecipeCard>
          ))}
        </div>
        <div>
          <h2>Food Around the World</h2>
          <MapChart data={plays} id="play" />
        </div>
      </GoogleContext.Provider>
    </Layout>
  );
}

export const pageQuery = graphql`
  query {
    allMdx(
      sort: { frontmatter: { date: DESC } }
      filter: { frontmatter: { category: { eq: "food" } } }
    ) {
      edges {
        node {
          id
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            slug
            title
            short
            category
            subcategory
            description
            featuredImage {
              childImageSharp {
                gatsbyImageData(width: 800)
              }
            }
          }
        }
      }
    }
  }
`;
